.btn {
  cursor: pointer;
  background-color: #9c88ff;
  box-shadow: var(--box-shadow);
  color: #fff;
  border: 0;
  display: block;
  font-size: 16px;
  margin: 10px 0 30px;
  padding: 10px;
  width: 100%;
}

.btn:focus,
.delete-btn:focus {
  outline: 0;
}

.list {
  list-style-type: none;
  padding: 0;
  margin-bottom: 40px;
}

.list li {
  background-color: #fff;
  box-shadow: var(--box-shadow);
  color: #333;
  display: flex;
  justify-content: space-between;
  position: relative;
  padding: 10px;
  margin: 10px 0;
}

.list li.plus {
  border-right: 5px solid #2ecc71;
}

.list li.minus {
  border-right: 5px solid #c0392b;
}

.delete-btn {
  cursor: pointer;
  background-color: #e74c3c;
  border: 0;
  color: #fff;
  font-size: 20px;
  line-height: 20px;
  padding: 2px 5px;
  position: absolute;
  top: 50%;
  left: 0;
  transform: translate(-100%, -50%);
  opacity: 0;
  transition: opacity 0.3s ease;
}

.list li:hover .delete-btn {
  opacity: 1;
}
.footer {
  text-align: center;
  font-weight: bold;
}

.swal-modal,
.swal-icon {
  background-color: rgba(21, 34, 81, 0.8);
  color: var(--font-color);
}
.swal-icon {
  display: none;
}

.swal-title,
.swal-text {
  color: var(--font-color);
}

.swal_text {
  margin: 0;
}
.swal-button {
  background-color: var(--primary-color);
  border-radius: 0.2rem;
  outline: none;
  box-shadow: none;
}
.swal-button:hover {
  box-shadow: none;
}
.d-flex {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.d-flex-direction {
  display: flex;
  flex-direction: column;
}

.appSideContent {
  margin-left: 64px;
}
@media (max-width: 500px) {
  .appSideContent {
    margin-left: 0;
  }
}
